<template>
  <div class="box">
    <Title :title="title" />
    <Tags />
    <div class="" v-if="error">{{ error }}</div>

    <div class="columns mt-2 is-multiline">
      <div
        class="column is-4-desktop is-3-tablet is-12-mobile"
        v-for="manga in Mangas"
        :key="manga.id"
      >
        <router-link
          class="card"
          :to="{ name: 'Manga', params: { title: manga.title, url: url } }"
        >
          <div class="card-image">
            <img :src="manga.image" :alt="manga.name" class="image " />
          </div>
          <div class="has-background-black ">
            <p
              class="has-text-white is-size-6 is-capitalized has-text-centered"
            >
              {{ manga.title }}
            </p>
            <p class="has-text-white is-size-7 has-text-centered">
              {{ manga.author }}
            </p>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import Title from './Title.vue'
import Tags from './Tags.vue'
import getMangas from '../composables/getMangas.js'
import { ref } from '@vue/reactivity'
import env from '@/env.js'

export default {
  name: 'truyện chọn lọc',
  components: { Title, Tags },
  data() {
    return {
      title: 'truyện đề cử',
    }
  },
  setup() {
    const url = ref(
      'https://jikan1.p.rapidapi.com/magazine/1/1?rapidapi-key=' + env.apiKey
    )

    const { Mangas, error, load } = getMangas(url.value)
    load()

    return { Mangas, error, url }
  },
}
</script>

<style scoped>
img {
  width: 100%;
  max-height: 250px;
}
</style>
