<template>
  <div class="container mt-3">
    <div class="columns is-multiline">
      <div class="column is-8-desktop is-12-tablet is-12-mobile">
        <!-- <div class="" v-if="error">{{ error }}</div> -->
        <TypeManga :MangaList="MangaList" :genreId="genreId" :url="url" />
        <Pagination @update-page="updatePage" />
      </div>
      <div class="column is-4-desktop">
        <Categories />
        <GoodManga />
      </div>
    </div>
  </div>
</template>

<script>
import TypeManga from '../components/TypeManga.vue'
import Categories from '../components/Categories.vue'
import GoodManga from '../components/GoodManga.vue'
import Pagination from '../components/Pagination.vue'
import usePagination from '../composables/usePagination.js'
import { ref } from '@vue/reactivity'

export default {
  name: 'Type',

  props: ['genreId'],
  components: { TypeManga, Categories, GoodManga, Pagination },
  setup(props) {
    const pageNum = ref(1)

    const updatePage = (e) => {
      pageNum.value = e
      loadPage(pageNum.value)
    }

    const { MangaList, loadPage, error, url } = usePagination(props.genreId)

    loadPage(pageNum.value)

    return { MangaList, error, updatePage, url }
  },
}
</script>

