<template>
  <div class="box mt-2">
    <Title :title="FiveNewChaps" />
    <ul class="block">
      <li class="subtitle">Chương 1 - {{ Manga.title }}</li>
    </ul>
  </div>
  <div class="box mt-2">
    <Title :title="ListChap" />
    <ul class="block">
      <li class="subtitle">Chương 1 - {{ Manga.title }}</li>
    </ul>
  </div>
</template>

<script>
import Title from './Title.vue'
export default {
  components: { Title },
  props: ['Manga'],
  data() {
    return {
      FiveNewChaps: '5 Chương mới nhất',
      ListChap: 'Danh sách chương truyện',
      chaps: [
        { chap: 1, title: 'Địa ngục linh chi' },
        { chap: 2, title: 'Địa ngục linh chi' },
        { chap: 3, title: 'Địa ngục linh chi' },
        { chap: 4, title: 'Địa ngục linh chi' },
        { chap: 5, title: 'Địa ngục linh chi' },
        { chap: 6, title: 'Địa ngục linh chi' },
        { chap: 7, title: 'Địa ngục linh chi' },
        { chap: 8, title: 'Địa ngục linh chi' },
      ],
    }
  },
  computed: {
    FiveChaps() {
      const newChap = this.chaps
      const reveseChap = newChap.reverse()
      return reveseChap.slice(0, 5)
    },
  },
}
</script>

