<template>
  <div class="box">
    <Title :title="title" />
    <section class="block is-flex is-justify-content-space-around">
      <ul>
        <li
          v-for="(item, i) in Left"
          :key="i"
          class="subtitle is-6 is-capitalized"
        >
          <router-link :to="{ name: 'Type', params: { genreId: item.id } }">
            <span class="icon"><i class="fas fa-tag"></i></span>
            {{ item.name }}
          </router-link>
        </li>
      </ul>
      <ul>
        <li
          v-for="(item, i) in Right"
          :key="i"
          class="subtitle is-6 is-capitalized"
        >
          <router-link :to="{ name: 'Type', params: { genreId: item.id } }">
            <span class="icon"><i class="fas fa-tag"></i></span>
            {{ item.name }}
          </router-link>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
import Title from './Title.vue'
import getGenre from '../composables/getGenre.js'
export default {
  components: { Title },
  data() {
    return {
      title: 'thể loại truyện',
    }
  },
  computed: {
    Left() {
      return this.allGenre.slice(0, this.allGenre.length / 2 - 1)
    },
    Right() {
      return this.allGenre.slice(this.allGenre.length / 2)
    },
  },
  setup() {
    const { loadGenre, allGenre } = getGenre(
      'https://jikan1.p.rapidapi.com/magazine/1/1?rapidapi-key='
    )
    loadGenre()

    return { allGenre }
  },
}
</script>

