import { ref } from 'vue'

const getMangas = (url) => {
  const Mangas = ref({})
  const error = ref(null)
  const error2 = ref(null)
  const Manga = ref([])

  const formartData = (Data) => {
    console.log(Data)
    const formatedData = Data.map((item) => {
      const author = item.authors[0].name
      const id = item.mal_id
      const image = item.image_url
      const series = item.serialization
      const desc = item.synopsis
      const title = item.title
      const url = item.url
      const type = item.type
      const genres = item.genres.map((genre) => genre.name).toString()
      const genreId = item.genres.map((genre) => genre.mal_id)
      const score = item.score

      return {
        author,
        image,
        series,
        desc,
        title,
        url,
        type,
        id,
        genres,
        genreId,
        score,
      }
    })
    return formatedData
  }

  const load = async () => {
    try {
      const res = await fetch(url)
      const data = await res.json()
      Mangas.value = await formartData(data.manga)
    } catch (err) {
      error.value = err
    }
  }

  const load2 = async (title) => {
    try {
      const res = await fetch(url)
      const data = await res.json()
      const temp = await formartData(data.manga)
      Manga.value = await temp.find((manga) => manga.title === title.trim())
      console.log(url)
    } catch (err) {
      error2.value = err
    }
  }

  return { Mangas, load, error, load2, error2, Manga }
}

export default getMangas
