import { ref } from 'vue'
import env from '@/env.js'

const usePagination = (genreId) => {
  const MangaList = ref({})
  const error = ref(null)
  const url = ref('')

  const formartData = (Data) => {
    // console.log(Data)
    const formatedData = Data.map((item) => {
      const author = item.authors[0].name
      const id = item.mal_id
      const image = item.image_url
      const series = item.serialization
      const desc = item.synopsis
      const title = item.title
      const url = item.url
      const type = item.type
      const genres = item.genres.map((genre) => genre.name).toString()
      const genreId = item.genres.map((genre) => genre.mal_id)
      const chap = item.volumes

      return {
        author,
        image,
        series,
        desc,
        title,
        url,
        type,
        id,
        genres,
        genreId,
        chap,
      }
    })
    return formatedData
  }

  const loadPage = async (pageNum) => {
    url.value = `https://jikan1.p.rapidapi.com/genre/manga/${genreId}/${pageNum}?rapidapi-key=${env.apiKey}`
    try {
      const res = await fetch(url.value)
      const data = await res.json()
      MangaList.value = await formartData(data.manga)
      // console.log(pageNum)
      // console.log(MangaList)
      // console.log(url.value)
    } catch (err) {
      error.value = err
    }
  }

  return { MangaList, loadPage, error, url }
}

export default usePagination
