import { ref } from 'vue'
import env from '@/env.js'

const getGenre = (url) => {
  const Mangas = ref({})
  const error = ref(null)
  const allGenre = ref([])

  const Types = (Data) => {
    const singleGenre = []
    Data.map((item) => {
      const genres = item.genres.map((genre) => {
        return { id: genre.mal_id, name: genre.name }
      })
      singleGenre.push(...genres)

      return singleGenre
    })
    const uniqueGenre = Array.from(
      singleGenre.reduce((map, obj) => map.set(obj.id, obj), new Map()).values()
    )

    return uniqueGenre
  }

  const formartData = (Data) => {
    console.log(Data)
    const formatedData = Data.map((item) => {
      const author = item.authors[0].name
      const id = item.mal_id
      const image = item.image_url
      const title = item.title
      const url = item.url
      const type = item.type
      const genres = item.genres.map((genre) => genre.name).toString()
      const genreId = item.genres.map((genre) => genre.mal_id)

      return {
        author,
        image,
        series,
        desc,
        title,
        url,
        type,
        id,
        genres,
        genreId,
      }
    })
    return formatedData
  }

  const loadGenre = async () => {
    try {
      const res = await fetch(url + env.apiKey)
      const data = await res.json()
      allGenre.value = Types(data.manga)
      // console.log(allGenre)
    } catch (err) {
      error.value = err
    }
  }

  const load = async () => {
    try {
      const res = await fetch(url + env.apiKey)
      const data = await res.json()
      Mangas.value = await formartData(data.manga)
    } catch (err) {
      error.value = err
    }
  }

  return { Mangas, load, loadGenre, allGenre }
}

export default getGenre
