<template>
  <div class="box">
    <Title :title="title" />
    <div
      class="is-flex is-justify-content-space-between my-2"
      v-for="manga in MangaList"
      :key="manga.id"
    >
      <router-link
        class="media"
        :to="{ name: 'Manga', params: { title: manga.title, url: url } }"
      >
        <div class="media-left">
          <img :src="manga.image" :alt="manga.title" class="image is-64x64" />
        </div>
        <div class="media-content">
          <h5
            class="is-size-5-desktop is-size-6-mobile has-text-weight-semibold is-capitalized"
          >
            {{ manga.title }}
            <span
              class="button is-outlined is-link is-small mr-1"
              v-if="manga.hot"
              >HOT</span
            >
            <span
              class="button is-outlined is-success is-small"
              v-if="manga.full"
              >FULL</span
            >
          </h5>
          <p class="subtitle is-6 is-capitalized">
            <span class="icon"><i class="fas fa-user"></i></span>
            {{ formartName(manga.author) }}
          </p>
        </div>
      </router-link>
      <div class="block">
        <h5 class="is-size-6 has-text-weight-bold">
          {{ setChap(manga) }}
        </h5>
        <p class="is-size-7">{{ manga.time }} giờ</p>
      </div>
    </div>
  </div>
</template>

<script>
import Title from './Title.vue'
export default {
  components: { Title },
  props: ['MangaList', 'genreId', 'url'],
  methods: {
    setChap(manga) {
      if (!manga.chap) {
        return 'Full'
      } else {
        return manga.chap + ' Chương'
      }
    },
    formartName(name) {
      const Arr = name.split(',')
      return Arr.join('')
    },
  },
  data() {
    return {
      title: 'list truyện ',
    }
  },
}
</script>

<style></style>
