<template>
  <nav class="pagination " role="navigation" aria-label="pagination">
    <ul class="pagination-list" ref="root">
      <li v-for="(page, i) in pages" :key="page" @click="changePage(page)">
        <span
          class="pagination-link"
          :id="i"
          @click="setActive(i)"
          :class="setPage1Active(1)"
          >{{ page }}</span
        >
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      pages: 10,
    }
  },
  methods: {
    changePage(page) {
      this.$emit('update-page', page)
    },
    setActive(index) {
      const items = document.querySelectorAll('.pagination-link')
      for (let i = 0; i < items.length; i++) {
        if (i === index) {
          items[i].classList.add('is-current')
        } else {
          items[i].classList.remove('is-current')
        }
      }
    },
    setPage1Active(i) {
      if (i === 0) {
        return 'is-current'
      }
    },
  },
}
</script>

<style></style>
